// components/ProofUpload.tsx
"use client";

import { useState } from "react";

export default function ProofUpload({
  listingId,
  defaultStatus = "unverified",
}: {
  listingId: number;
  defaultStatus?: "verified" | "unverified";
}) {
  const [files, setFiles] = useState<FileList | null>(null);
  const [busy, setBusy] = useState(false);
  const [msg, setMsg] = useState<string | null>(null);

  const onSubmit = async () => {
    if (!files || files.length === 0) {
      setMsg("파일을 선택해 주세요.");
      return;
    }
    setBusy(true);
    setMsg(null);
    try {
      // 데모: 실제 업로드 대신 /api/leads 로 이벤트 남김
      const meta = Array.from(files).map((f) => ({
        name: f.name,
        size: f.size,
        type: f.type,
      }));
      const res = await fetch("/api/leads", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          type: "listing",
          listingId,
          name: "증빙 업로드",
          phone: "-",
          payload: { kind: "proof_upload", files: meta, status: defaultStatus },
        }),
      });
      const data = await res.json();
      if (!res.ok) throw new Error(data?.error || "업로드 요청 실패");
      setMsg("업로드 요청이 접수되었습니다. (데모)");
      setFiles(null);
    } catch (e: any) {
      setMsg(e?.message || "업로드 요청 실패");
    } finally {
      setBusy(false);
    }
  };

  return (
    <div className="flex items-center gap-2">
      <label className="text-xs text-neutral-600">증빙 업로드</label>
      <input
        type="file"
        multiple
        className="text-xs"
        onChange={(e) => setFiles(e.target.files)}
      />
      <button
        onClick={onSubmit}
        disabled={busy}
        className="rounded-md border px-2 py-1 text-xs hover:bg-neutral-50 disabled:opacity-60"
      >
        {busy ? "처리 중…" : "업로드"}
      </button>
      {msg && <span className="text-xs text-neutral-500">{msg}</span>}
    </div>
  );
}
